<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的评价</div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="待评价" name="first"></el-tab-pane>
      <el-tab-pane label="已评价" name="second"></el-tab-pane>
      <el-tab-pane label="评价计划" name="third"></el-tab-pane>
      <el-tab-pane label="被评价课程" name="fourth"></el-tab-pane>
    </el-tabs>
    <span class="viewchange fr" @click="gotoCalendar" v-if="activeName=='first'">
      <i class="el-icon-date" /> 按周历查看
    </span>
    <div class="personnal-detail-wrap-container">
      <evaluate-card
        v-for="(c,i) in cards"
        @GetInto="GetInto"
        :key="i"
        :card="c"
        :btnName="c.btnTitle"
        ref="evaluate"
      />
      <div v-if="cards.length==0" style="margin-top:30px">暂无数据</div>
    </div>
    <bottom-pager :total="total" :pageSize="pageSize" :currentPage="currentPage" @change="pageChange" ref="bottomPager" />
    <el-dialog
      class="planFrom"
      :title="planTitle"
      :visible.sync="lessonTableVisible"
      :close-on-click-modal="false"
      :before-close="handleClose"
    >
      <span>{{planInfo}}</span>
      <span>{{planCount}}</span>
      <span>{{PJCount}}</span>
      <span>{{evaltype}}</span>
      <span>{{evalobj}}</span>
      <div class="block">
        <span class="demonstration">选取范围</span>
        <el-date-picker
          v-model="timeFrame"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          @change="pickerOptions"
        ></el-date-picker>
      </div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column label="课程名称" property="name"></el-table-column>
        <el-table-column label="排课日期" property="date"></el-table-column>
        <el-table-column label="课节" property="lesson"></el-table-column>
        <el-table-column label="排课教室" property="room"></el-table-column>
        <el-table-column label="授课老师" property="thc"></el-table-column>
        <el-table-column label="班级" property="class"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">进入</el-button>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="primary" @click="offlineDialogVisible = true">线下课</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="线下课基本信息"
      :visible.sync="offlineDialogVisible"
      :close-on-click-modal="false"
      :before-close="offlineHandleClose"
      append-to-body
      @open="offlineFormOpen"
    >
      <div>
        <el-form ref="form" :rules="rules" :model="form" label-width="80px">
          <el-row :gutter="20">
            <el-col :span="8">
              <el-form-item label="排课日期" prop="date">
                <el-date-picker v-model="form.date" type="date" placeholder="选择日期"></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="课节" prop="lesson">
                <el-select v-model="form.lesson" placeholder="请选择">
                  <el-option
                    v-for="item in lessonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="排课教室" prop="clsRoom">
                <el-select v-model="form.clsRoom" filterable placeholder="请选择">
                  <el-option
                    v-for="item in roomOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">
              <el-form-item label="课程名称" prop="course">
                <el-select v-model="form.course" filterable placeholder="请选择">
                  <el-option
                    v-for="item in courOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="授课老师" prop="teacher">
                <el-select v-model="form.teacher" filterable placeholder="请选择">
                  <el-option
                    v-for="item in thcOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="班级" prop="class">
                <el-select
                  v-model="form.class"
                  @change="setofflineclassname"
                  filterable
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in classOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span>量表模板信息</span>
      <scale
        class="evaluate-scale"
        :textarea="text"
        :trees="scals"
        :recid="hasrecid"
        :bgSrc="bgSrc"
        :isshow="isPicshow"
        :classname="offlineclassname"
        :submitdisabled="scalesubmitdisabled"
        :fileList="picList"
        @markscore="SaveGauge"
        @setRFStuCount="setRFStuCount"
        @setRFLateStuCount="setRFLateStuCount"
        @setRFLEStuCount="setRFLEStuCount"
        @setRFAbsentStuCount="setRFAbsentStuCount"
        @setRFLeaveStuCount="setRFLeaveStuCount"
      />
    </el-dialog>
    <el-dialog
      title="查看"
      :close-on-click-modal="false"
      :visible.sync="SeePlanFormShow"
      :before-close="SeeHandleClose"
    >
      <el-tabs v-model="seeActiveName" type="border-card">
        <el-tab-pane name="first" label="基本信息">
          <div v-for="(item,i) in CourseInfo" :key="i" style="text-align:left;">
            <el-row :gutter="20">
              <el-col :span="3" class="row-title">
                <div>计划类型：</div>
              </el-col>
              <el-col :span="6" class="row-title">
                <div>{{item.type}}</div>
              </el-col>
            </el-row>
            <el-row :gutter="20">
              <el-col :span="3" class="row-title">
                <div>评价实体：</div>
              </el-col>
              <el-col :span="6" class="row-title">
                <div>{{item.entity}}</div>
              </el-col>
            </el-row>
            <el-row :gutter="0" class="row-title">
              <el-col :span="4">
                <div>评价起止时间：</div>
              </el-col>
              <el-col :span="7">
                <div>{{item.date}}</div>
              </el-col>
              <el-col :span="6"></el-col>
            </el-row>
            <el-row :gutter="20">
              <el-col :span="3" class="row-title">
                <div>评价范围：</div>
              </el-col>
              <el-col :span="10" class="row-title">
                <div>{{item.range}}</div>
              </el-col>
            </el-row>
            <el-row :gutter="20" v-if="isSpanScore">
              <el-col :span="3" class="row-title">
                <div>已评分：</div>
              </el-col>
              <el-col :span="20" class="row-title">
                <span v-for="(s,i) in item.score" :key="i">{{s}}</span>
              </el-col>
            </el-row>
          </div>
        </el-tab-pane>
        <el-tab-pane name="second" class="rateForm" label="量表信息">
          <table class="detail-table" v-for="(item,i) in trees" :key="i">
            <caption class="table-caption">{{item.GaugeName}}</caption>
            <thead>
              <tr>
                <th style="width:65%">评估内容</th>
                <!-- <th>评分标准</th> -->
                <th style="width:15%">分值</th>
                <th style="width:20%" v-if="isScore">打分</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(sub,j) in item.children" :key="j">
                <td>{{sub.GIName}}：{{sub.GIContent}}</td>
                <!-- <td>
            <p class="tb-second">{{sub.GIContent}}</p>
                </td>-->
                <td>{{sub.GIScore}}</td>
                <td v-if="isScore">{{sub.GSScore}}</td>
              </tr>
              <tr class="trdesc">
                <td colspan="3">评语：{{item.GSDesc}}</td>
              </tr>
            </tbody>
          </table>
          <p>总评语：{{RFDesc}}</p>
        </el-tab-pane>
        <el-tab-pane label="课堂信息" v-if="isScore||iscs">
          <div class="ed-right-wrap fr">
            <div class="kbxx">
              <span>课程名称：{{courseName}}</span>
              <span>排课时间：{{courseTime}}</span>
              <span>教室：{{clsroomName}}</span>
            </div>
            <div class="kbxx">
              <span>授课老师：{{csteacherName}}</span>
              <span style="width:66.6%;">上课班级：{{csclassName}}</span>
            </div>
            <div class="clear" />
            <div class="ed-head" v-if="isScore">学生考勤</div>
            <div class="kqxx" v-if="isScore">
              <span>应到学生：{{RFStuCount}}人</span>
              <span>迟到：{{RFLateStuCount}}人</span>
              <span>早退：{{RFLEStuCount}}人</span>
              <span>缺席：{{RFAbsentStuCount}}人</span>
              <span>请假：{{RFLeaveStuCount}}人</span>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="附件" v-if="isScore">
          <div class="ed-right-wrap fr">
            <div class="ed-head">附件</div>
            <div class="imgs">
              <el-image
                style="width: 90px; height: 60px"
                v-for="(item,i) in srcList"
                :src="item"
                :key="i"
                :preview-src-list="srcList"
              ></el-image>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import cookie from "../../../../utils/cookieOperate.js";
import evaluateCard from "../../../../components/personal/teacher/evaluateCard.vue";
import bottomPager from "../../../../components/personal/bottomPager.vue";
import scale from "../../../../components/course/detailScale.vue";
import { formatDate } from "../../../../utils/utils.js";
export default {
  components: {
    evaluateCard,
    bottomPager,
    scale
  },
  data() {
    return {
      userid: "",
      activeName: "first",
      seeActiveName: "first",
      btnName: "进入",
      total: 0,
      pageSize: 20,
      isOperate: true,
      cards: [],
      lessonTableVisible: false,
      offlineDialogVisible: false,
      planTitle: "",
      planInfo: "",
      planCount: "",
      PJCount: "",
      evaltype: "",
      evalobj: "",
      timeFrame: [
        new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
        new Date()
      ],
      tableData: [],
      trees: [],
      isScore: true,
      iscs: true,
      isSpanScore: true,
      ID: "",
      eID: "",
      gID: "",
      eType: "",
      form: {
        date: "",
        lesson: "",
        clsRoom: "",
        course: "",
        teacher: "",
        class: ""
      },
      rules: {
        date: [{ required: true, message: "请选择日期", trigger: "blur" }],
        lesson: [{ required: true, message: "请选择课节", trigger: "blur" }],
        clsRoom: [{ required: true, message: "请选择教室", trigger: "blur" }],
        course: [{ required: true, message: "请选择课程", trigger: "blur" }],
        teacher: [{ required: true, message: "请选择教师", trigger: "blur" }],
        class: [{ required: true, message: "请选择班级", trigger: "blur" }]
      },
      lessonOptions: [],
      roomOptions: [],
      courOptions: [],
      thcOptions: [],
      classOptions: [],
      text: "",
      scals: [],
      hasrecid: "",
      bgSrc: "",
      isPicshow: false,
      scalesubmitdisabled: false,
      picList: [],
      Piclist: "",
      SeePlanFormShow: false,
      CourseInfo: [],
      TermStart: "",
      TermEnd: "",
      offlineclassname: "",
      RFStuCount: 0,
      RFLateStuCount: 0,
      RFLEStuCount: 0,
      RFAbsentStuCount: 0,
      RFLeaveStuCount: 0,
      RFDesc: "",
      courseName: "",
      courseTime: "",
      clsroomName: "",
      csteacherName: "",
      csclassName: "",
      srcList: [],
      currentPage:1
    };
  },
  mounted() {
    if(this.$route.query.userName && this.$route.query.userPwd) {  
      this.username = this.$route.query.userName;
      this.userpwd = this.$route.query.userPwd;
      this.loginAjax(this.username, this.userpwd); 
    } 
    if (http.logined()) {
      // 已登录
      let userInfo = http.getUser();
      if (userInfo) {
        this.userid = http.getUser().UserID;
      }
    } 
    if (this.$route.query.tabname) 
      this.activeName = this.$route.query.tabname; 
    this.currentPage = 1;
    this.GetThcRatingRecTask();
  },
  methods: {
    async loginAjax (user, psd) {
      let res = await http.post(api.login, {
        name: user,
        pwd: psd,
        from: '11'
      })
      this.loading = false
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        })
      } else {
        var UserType = res.UserInfo.UserType;
        cookie.set(api.cookie.user_infor, JSON.stringify(res.UserInfo));
        cookie.set(api.cookie.token, res.Data);
        cookie.set("loginType", "1");
        if (UserType == 1) {
          cookie.set(api.cookie.thc_infor, JSON.stringify(res.Teacher));
        } else {
          cookie.set(api.cookie.stu_infor, JSON.stringify(res.Student));
        }

        this.$router.push('/main/teacherSpace/teacherEvaluate/list')
      }
    },
    gotoCalendar() {
      this.$router.push("/main/teacherSpace/teacherEvaluate/Calendar");
    },
    tabClick(val) {
      this.activeName = val.name;
      this.currentPage = 1;
      this.$router.push({
        query: null
      });
      this.GetThcRatingRecTask();
    },
    async GetThcRatingRecTask() {
      let type = 1;
      let tabindex = 1;
      let zt = 0;
      switch (this.activeName) {
        case "first":
          this.btnName = "进入";
          tabindex = 1;
          zt = 1;
          break;
        case "second":
          zt = 2;
          tabindex = 2;
          this.btnName = "查看";
          break;
        case "third":
          zt = 3;
          tabindex = 3;
          this.btnName = "查看";
          break;
        case "fourth":
          type = 2;
          tabindex = 4;
          this.btnName = "查看";
          break;
      }
      const res = await http.get(api.getThcRatingRecTask, {
        uid: this.userid,
        zt: zt,
        type: type,
        key: "",
        sfield: "EVALPEnd",
        sdir: 0,
        estate: "all",
        psize: this.pageSize,
        pno: this.currentPage
      });
      this.cards = [];
      if (!res.Result) {
        
      } else {
        this.total = res.RowsCount;
        if (res.Datas != null) {
          this.isOperate = false;
          res.Datas.forEach(element => {
            let uid = this.userid;
            if (type === 2) {
              uid = element.Value[0].userid;
            }
            let desc = [];
            let PlanDetail = null;
            let erange = "";
            switch (element.Key.EVALPRange) {
              case 0:
                erange = "不限";
                break;
              case 1:
                erange = "按老师";
                break;
              case 2:
                erange = "按部门";
                break;
            } 
            if (element.Key.EVALPType == 1) {
              PlanDetail = {
                lessonsDate: formatDate(
                  new Date(element.Value[0].lessonsDate),
                  "yyyy-MM-dd"
                ),
                lesson: "第 " + element.Value[0].lesson + " 节",
                clsroomname: element.Value[0].clsroomname,
                teachername: element.Value[0].teachername,
                coursename: element.Value[0].coursename,
                classname: element.Value[0].classname,
                PlanDateRane:
                  formatDate(element.Key.EVALPStart, "yyyy-MM-dd") +
                  "至" +
                  formatDate(element.Key.EVALPEnd, "yyyy-MM-dd"),
                EVALPCount: element.Key.EVALPCount,
                 EVALPRange: erange 
              };
            } else {
              var objname = "";
              // eslint-disable-next-line
              var tylename = "";
              if (element.Key.EVALPType == 2) {
                tylename = "老师";
                objname = element.Value[0].teachername;
              }
              if (element.Key.EVALPType == 3) {
                tylename = "课程";
                objname = element.Value[0].coursename;
              }

              PlanDetail = {
                PlanDateRane:
                  formatDate(element.Key.EVALPStart, "yyyy-MM-dd") +
                  "至" +
                  formatDate(element.Key.EVALPEnd, "yyyy-MM-dd"),
                EVALPCount: element.Key.EVALPCount,
                objname: objname,
                 EVALPRange: erange
              };
            }
            if (this.activeName == "second") {
              if (element.Value[0].rfestate == "3") this.btnName = "修改";
              else this.btnName = "查看";
              desc.push("评价时间：" + element.Value[0].rfcreatedate);
              desc.push("平均分数：" + element.Value[0].avgScore);
            }
            let rfcsid = 0;
            if (this.activeName == "fourth") {
              rfcsid = element.Value[0].rfcsid == "" ? 1 : 0;
            }
            var canenter = true;
            var csstart = new Date(element.Value[0].lessonsTime).getTime();
            var nowtime = new Date().getTime();
            if (element.Key.EVALPType == 1 && csstart > nowtime) {
              canenter = false;
            }
            var evalpClassify = "";
            switch (element.Key.EVALPClassify) {
              case "1":
                evalpClassify = "百门优质课程评审";
                break;
              case "2":
                evalpClassify = "精彩一堂课";
                break;
              case "3":
                evalpClassify = "职称评定";
                break;
              case "4":
                evalpClassify = "通识教育选修课";
                break;
              case "5":
                evalpClassify = "全英文课程";
                break;
              case "6":
                evalpClassify = "翻转课堂";
                break;
              case "7":
                evalpClassify = "随机听课";
                break;
            }

            var tedate = new Date(
              formatDate(element.Key.EVALPEnd, "yyyy-MM-dd")
            );
            var now = new Date();
            var days = tedate.getTime() - now.getTime();
            var day = parseInt(days / (1000 * 60 * 60 * 24));
            this.cards.push({
              id: element.Value[0].id,
              eID: element.Key.EVALPID,
              gID: element.Key.GaugeID,
              title: element.Key.EVALPName,
              className: element.Value[0].classname,
              clsName: element.Value[0].clsroomname,
              courseName: element.Value[0].coursename,
              csDate: formatDate(
                new Date(element.Value[0].lessonsDate),
                "yyyy-MM-dd"
              ),
              teacherID: element.Value[0].teacherid,
              teacherName: element.Value[0].teachername,
              stardate: formatDate(element.Key.EVALPStart, "yyyy-MM-dd"),
              enddate: formatDate(element.Key.EVALPEnd, "yyyy-MM-dd"),
              pjcount: element.Value[0].pjcount,
              subject: element.Key.subject,
              evlapType: element.Key.EVALPType,
              type: type,
              tabindex: tabindex,
              uid: uid,
              rfid: element.Value[0].rfid,
              range: element.Key.EVALPRange,
              csLesson: element.Value[0].lesson,
              rfcsid: rfcsid,
              score: element.Value[0].userScore,
              avgScore: element.Value[0].avgScore,
              rfcreatedate: element.Value[0].rfcreatedate,
              rfestate: element.Value[0].rfestate,
              btnTitle: this.btnName,
              canenter: canenter,
              lessonsTime: element.Value[0].lessonsTime,
              descs: desc,
              PlanDetail: PlanDetail,
              evalpClassify: evalpClassify,
              day: day,
              zt:zt
            });
          });
        }
        if (this.cards.length == 0) {
          this.$message({
            showClose: true,
            message: "暂无数据",
            type: "warning"
          });
        }
      }
    },
    pageChange(page) {
      this.currentPage = page;
      this.GetThcRatingRecTask();
    },
    GetInto(row) {
      switch (this.activeName) {
        case "first":
          switch (row.evlapType) {
            case 1:
              //课表
              this.$router.push({
                name: `detail`,
                query: {
                  id: row.id,
                  gid: row.gID,
                  eid: row.eID,
                  atype: "1",
                  uid: this.userid,
                  className: row.className,
                  clsRoom: row.clsName,
                  courseName: row.courseName,
                  csDate: row.lessonsTime,
                  thcName: row.teacherName,
                  thcID: row.teacherID,
                  lesson: row.csLesson
                }
              });
              break;
            case 2:
            case 3:
              //老师
              //课程
              this.lessonTableVisible = true;
              this.planTitle = row.title;
              this.planInfo = row.descs[0];
              this.planCount = row.descs[1];
              if (row.evlapType == 1) {
                this.evaltype = row.descs[8];
                this.evalobj = row.descs[9];
              } else {
                this.evaltype = row.descs[2];
                this.evalobj = row.descs[3];
              }
              this.PJCount = "已评价 " + row.pjcount + " 次";
              this.ID = row.id;
              this.eID = row.eID;
              this.gID = row.gID;
              this.eType = row.evlapType;
              this.getCurrentTerm();
              this.GetEvalPlanData();
              break;
            default:
              break;
          }
          break;
        case "second":
          if (row.rfestate == "3") {
            // this.GetRatingFormDataByEPID(row.eID);
            this.$router.push({
              name: `detail`,
              query: {
                id: row.rfcsid,
                gid: row.gID,
                eid: row.eID,
                atype: "2",
                uid: this.userid,
                className: row.className,
                clsRoom: row.clsName,
                courseName: row.courseName,
                csDate: row.lessonsTime,
                thcName: row.teacherName,
                thcID: row.teacherID,
                lesson: row.csLesson
              }
            });
          } else {
            this.SeePlanFormShow = true;
            this.GetEvalData(row.eID, row.range);
            this.GetSeeGauge(row.gID);
            this.GetRatingFormDataByEPID(row.eID);
            this.isScore = true;
            this.isSpanScore = false;
            setTimeout(() => {
              this.GetGSSocre(this.userid, row.eID);
            }, 500);
          }
          break;
        case "third":
          this.SeePlanFormShow = true;
          this.GetEvalData(row.eID, row.range);
          this.GetSeeGauge(row.gID);
          this.isScore = false;
          this.isSpanScore = false;
          if (row.evlapType == "1") {
            this.iscs = true;
            this.courseName = row.courseName;
            var temptime = row.csDate;
            var templesson =
              row.csLesson == null ? "" : "第" + row.csLesson + "节";
            this.courseTime = temptime + " " + templesson;
            this.clsroomName = row.clsName;
            this.csteacherName = row.teacherName;
            this.csclassName = row.className;
          } else {
            this.iscs = false;
          }
          break;
        case "fourth":
          // this.SeePlanFormShow = true;
          // this.GetEvalData(row.eID, row.range);
          // this.GetSeeGauge(row.gID);
          // this.isScore = false;
          // this.isSpanScore = true;
          // this.GetWholeGSSocre(this.userid, row.eID);
          if (row.evlapType == 1) {
            this.evaltype = row.descs[8];
            this.evalobj = row.descs[9];
          } else {
            this.evaltype = row.descs[2];
            this.evalobj = row.descs[3];
          }
          this.$router.push({
            name: `evaluateddetail`,
            query: {
              title: row.courseName,
              gid: row.gID,
              evalpid: row.eID,
              evalpname: row.title,
              rfdate: row.rfcreatedate,
              stardate: row.stardate,
              enddate: row.enddate,
              teacher: row.teacherName,
              evlapType: row.evlapType,
              evalobj: this.evalobj,
              evaltypename: this.evaltype
            }
          });
          break;
        default:
          break;
      }
    },
    pickerOptions() {
      let minDate = this.timeFrame[0].getTime();
      let maxDate = this.timeFrame[1].getTime();
      if (this.TermStart != "" && this.TermEnd != "") {
        let tstart = new Date(
          formatDate(this.TermStart, "yyyy-MM-dd")
        ).getTime();
        let tend = new Date(formatDate(this.TermEnd, "yyyy-MM-dd")).getTime();
        if (minDate < tstart || maxDate > tend) {
          this.$message({
            showClose: true,
            message:
              "只能查询本学期内：【" +
              formatDate(this.TermStart, "yyyy-MM-dd") +
              " ~ " +
              formatDate(this.TermEnd, "yyyy-MM-dd") +
              "】的课程",
            type: "warning"
          });
          this.timeFrame = [
            new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
            new Date()
          ];
          this.tableData = [];
          return;
        }
      }

      if (maxDate - minDate > 8.64 * Math.pow(10, 7) * 30) {
        this.$message({
          showClose: true,
          message: "时间跨度超过30天请重新选择",
          type: "warning"
        });
        this.timeFrame = [
          new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
          new Date()
        ];
        return;
      }
      this.GetEvalPlanData();
    },
    handleEdit(index, row) {
      var csstart = new Date(row.date).getTime();
      var nowtime = new Date().getTime();
      if (csstart > nowtime) {
        this.$message({
          showClose: true,
          message: "当前时间小于开始上课时间，无法进入评价",
          type: "warning"
        });
        return;
      }
      this.$router.push({
        name: `detail`,
        query: {
          id: row.csid,
          gid: this.gID,
          eid: this.eID,
          uid: this.userid,
          atype: "1",
          className: row.class,
          clsRoom: row.room,
          courseName: row.name,
          csDate: row.date,
          thcName: row.thc,
          thcID: row.teacherID,
          lesson: row.lesson
        }
      });
    },
    handleClose() {
      this.ID = "";
      this.eID = "";
      this.gID = "";
      this.eType = "";
      this.timeFrame = [
        new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
        new Date()
      ];
      this.lessonTableVisible = false;
    },
    offlineHandleClose() {
      this.offlineDialogVisible = false;
      this.picList = [];
      this.Piclist = "";
      this.$refs["form"].resetFields();
      this.handleClose();
    },
    async getCurrentTerm() {
      const res = await http.get(api.getCurrentTerm, {
        token: http.getToken()
      });
      if (res.Datas != null) {
        this.TermStart = res.Datas.TermStart;
        this.TermEnd = res.Datas.TermEnd;
      }
    },
    async GetEvalPlanData() {
      let sdt = formatDate(this.timeFrame[0], "yyyy-MM-dd");
      let edt = formatDate(this.timeFrame[1], "yyyy-MM-dd");
      const res = await http.get(api.GetEvalPlanCsByID, {
        id: this.ID,
        eid: this.eID,
        sdate: sdt,
        edate: edt,
        etype: this.eType,
        token: http.getToken()
      });
      this.tableData = [];
      if (res.Datas != null && res.Datas.length > 0) {
        res.Datas.forEach(element => {
          var canenter = true;
          var csstart = new Date(element.Value[0].lessonTime).getTime();
          var nowtime = new Date().getTime();
          if (csstart > nowtime) {
            canenter = false;
          }
          this.tableData.push({
            csid: element.Key.CSID,
            name: element.Key.CSTitle,
            date: formatDate(element.Key.CSDate, "yyyy-MM-dd"),
            lesson: element.Key.CSLesson == null ? "" : element.Key.CSLesson,
            room: element.Value[0].ClsRoomName,
            thc: element.Value[0].TeacherName,
            class: element.Value[0].ClassName,
            eID: element.Key.EVALPID,
            teacherID: element.Value[0].TeacherID,
            gID: element.Key.GaugeID,
            canenter: canenter
          });
        });
      }
    },
    offlineFormOpen() {
      this.GetGauge();
      this.GetLesson();
      this.GetClsRoom();
      this.GetCourse();
      this.GetTeacher();
      this.GetClass();
    },
    async fetchPic(filename) {
      let downloadURL = await api.GetDownloadFileURL();
      this.bgSrc =
        downloadURL + "?filename=" + filename + "&fileType=12&platFormType=1";
    },
    SaveGauge(text, fileList) {
      if (this.userid) {
        this.$refs["form"].validate(valid => {
          if (valid) {
            if (fileList.length != 0) {
              let url = api.UploadFile;
              fileList.forEach(element => {
                let fd = new FormData();
                let guid = api.uuid();
                fd.append("stream", element);
                fd.append("type", 12);
                fd.append("picName", guid);
                let res = http.postFormData(url, fd);
                if (res.type) {
                  this.$message({
                    message: res.message,
                    type: res.type
                  });
                } else {
                  this.Piclist += guid + ",";
                }
              });
            }
            if (this.Piclist.length > 0) {
              this.Piclist = this.Piclist.substr(0, this.Piclist.length - 1);
            }
            this.saveEvalData(text, this.Piclist);
          } else {
            return false;
          }
        });
      } else {
        this.$message({
          showClose: true,
          message: "请先登录",
          type: "warning"
        });
      }
    },
    async saveEvalData(text, Piclist) {
      const res = await http.post(api.saveGauge, {
        uid: this.userid,
        evalpid: this.eID,
        csid: this.ID,
        clsroomname: this.form.clsRoom.label,
        csdate: formatDate(this.form.date, "yyyy-MM-dd"),
        cslesson: this.form.lesson,
        cstitle: this.form.course.label,
        teacherid: this.form.teacher.value,
        teachername: this.form.teacher.label,
        cslasss: this.form.class.label,
        text: text,
        msg: JSON.stringify(this.scals).toString(),
        filenames: Piclist,
        RFStuCount: this.RFStuCount,
        RFLateStuCount: this.RFLateStuCount,
        RFLEStuCount: this.RFLEStuCount,
        RFAbsentStuCount: this.RFAbsentStuCount,
        RFLeaveStuCount: this.RFLeaveStuCount
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.scalesubmitdisabled = true;
        this.text = text;
        this.offlineHandleClose();
        this.$message({
          showClose: true,
          message: "评价成功！",
          type: "success"
        });
      }
    },
    async GetGauge() {
      const res = await http.get(api.getGaugeData, {
        id: this.gID,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.scals = [];
        res.Item.forEach(element => {
          let child = [];
          element.Value.forEach(ele => {
            child.push({
              GIID: ele.GIID,
              GIName: ele.GIName,
              GIContent: ele.GIContent,
              GIScore: ele.GIScore,
              GSScore: 0,
              disabled: false
            });
          });
          if (element.Key) {
            this.scals.push({
              GaugeName: element.Key.GIName,
              GIID: element.Key.GIID,
              isFold: true,
              showHeadOpts: false,
              edit: false,
              children: child
            });
          }
        });
      }
    },
    async GetLesson() {
      const res = await http.get(api.GetLessionByDate, {
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.lessonOptions = [];
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(element => {
            this.lessonOptions.push({
              label:
                "第" +
                element.LessonSession +
                "节[" +
                element.LessonClsTime +
                "-" +
                element.LessonOutTime +
                "]",
              value: element.LessonSession
            });
          });
        }
      }
    },
    async GetClsRoom() {
      const res = await http.get(api.GetAllClsDatas, {
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.roomOptions = [];
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(element => {
            this.roomOptions.push({
              label: element.ClsRoomName,
              value: element.ClsRoomID
            });
          });
        }
      }
    },
    async GetCourse() {
      const res = await http.get(api.GetCourseByState, {
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.courOptions = [];
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(element => {
            this.courOptions.push({
              label: element.CourseName,
              value: element.CourseID
            });
          });
        }
      }
    },
    async GetTeacher() {
      const res = await http.get(api.GetThcByState, {
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.thcOptions = [];
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(element => {
            this.thcOptions.push({
              label: element.TeacherName,
              value: element.TeacherID
            });
          });
        }
      }
    },
    async GetClass() {
      const res = await http.get(api.ClsList, {
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.classOptions = [];
        if (res.Datas != null && res.Datas.length > 0) {
          res.Datas.forEach(element => {
            this.classOptions.push({
              label: element.ClassName,
              value: element.ClassID
            });
          });
        }
      }
    },
    SeeHandleClose() {
      this.seeActiveName = "first";
      this.SeePlanFormShow = false;
    },
    async GetEvalData(id, type) {
      this.CourseInfo = [];
      const res = await http.get(api.GetEVALCourseByID, {
        id: id,
        eType: type,
        token: http.getToken()
      });
      if (res.Data) {
        this.CourseInfo.push({
          type:
            res.Data.Key.EVALPType == 1
              ? "课表"
              : res.Data.Key.EVALPType == 2 ? "教师" : "课程",
          entity: JSON.parse(res.Data.Key.EVALPSID)[0].name,
          range: res.Data.content == "" ? "全体人员可评" : res.Data.content,
          date:
            formatDate(res.Data.Key.EVALPStart, "yyyy-MM-dd") +
            " 至 " +
            formatDate(res.Data.Key.EVALPEnd, "yyyy-MM-dd")
        });
      }
    },
    async GetRatingFormDataByEPID(eid) {
      const res = await http.get(api.GetRatingFormDataByEPID, {
        id: eid,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        if (res.Datas.length > 0) {
          res.Datas.forEach(element => {
            if (element.RatingForm.UserID == this.userid) {
              this.RFDesc = element.RatingForm.RFDesc;
              this.courseName = element.RatingForm.CSTitle;
              var temptime =
                element.RatingForm.CSDate == undefined
                  ? ""
                  : formatDate(element.RatingForm.CSDate, "yyyy-MM-dd");
              var templesson =
                element.RatingForm.CSLesson == null
                  ? ""
                  : "第" + element.RatingForm.CSLesson + "节";
              this.courseTime = temptime + " " + templesson;
              this.clsroomName = element.RatingForm.ClsRoomName;
              this.csteacherName = element.RatingForm.CSTeacher;
              this.csclassName = element.RatingForm.CSCls;
              this.RFStuCount =
                element.RatingForm.RFStuCount == null
                  ? 0
                  : element.RatingForm.RFStuCount;
              this.RFLateStuCount =
                element.RatingForm.RFLateStuCount == null
                  ? 0
                  : element.RatingForm.RFLateStuCount;
              this.RFLEStuCount =
                element.RatingForm.RFLEStuCount == null
                  ? 0
                  : element.RatingForm.RFLEStuCount;
              this.RFAbsentStuCount =
                element.RatingForm.RFAbsentStuCount == null
                  ? 0
                  : element.RatingForm.RFAbsentStuCount;
              this.RFLeaveStuCount =
                element.RatingForm.RFLeaveStuCount == null
                  ? 0
                  : element.RatingForm.RFLeaveStuCount;
              this.getRatingAttByRfid(element.RatingForm.RFID);
            }
          });
        }
      }
    },
    async getRatingAttByRfid(rfid) {
      let downloadURL = await api.GetDownloadFileURL();
      let res = await http.get(api.getRatingAttByRfid, {
        rfid: rfid,
        token: http.getToken()
      });
      if (res.Result) {
        if (res.Datas != null && res.Datas.length > 0) {
          this.srcList = [];
          res.Datas.forEach(element => {
            this.srcList.push(
              downloadURL +
                "?filename=" +
                element.RAName +
                "&fileType=12&platFormType=1"
            );
          });
        }
        console.log(this.srcList);
      }
    },
    async GetSeeGauge(id) {
      const res = await http.get(api.getGaugeData, {
        id: id,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.trees = [];
        res.Item.forEach(element => {
          let child = [];
          element.Value.forEach(ele => {
            child.push({
              GIID: ele.GIID,
              GIName: ele.GIName,
              GIContent: ele.GIContent,
              GIScore: ele.GIScore,
              GSScore: 0,
              disabled: false
            });
          });
          if (element.Key) {
            this.trees.push({
              GaugeName: element.Key.GIName,
              GIID: element.Key.GIID,
              isFold: true,
              showHeadOpts: false,
              edit: false,
              children: child
            });
          }
        });
      }
    },
    async GetGSSocre(uid, evalpid) {
      const res = await http.get(api.GetIndividualGsScoreByID, {
        uid: uid,
        evalpid: evalpid,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.trees.forEach(element => {
          element.children.forEach(ele => {
            res.Datas.forEach(res => {
              if (res.GaugeScore.GIID == ele.GIID)
                ele.GSScore = res.GaugeScore.GSScore;
              if (element.GIID == res.GaugeScore.GIID)
                element.GSDesc = res.GaugeScore.GSDesc;
            });
          });
        });
      }
    },
    async GetWholeGSSocre(id, evalpID) {
      const res = await http.get(api.GetGsScoreByID, {
        uid: id,
        evalpid: evalpID,
        token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.CourseInfo[0].score = {};
        if (res.Datas != null && res.Datas.length > 0) {
          let s = [];
          res.Datas.forEach(element => {
            s.push(parseInt(element.gsscore));
          });
          this.CourseInfo[0].score = s;
        }
      }
    },
    setRFStuCount(val) {
      this.RFStuCount = val;
    },
    setRFLateStuCount(val) {
      this.RFLateStuCount = val;
    },
    setRFLEStuCount(val) {
      this.RFLEStuCount = val;
    },
    setRFAbsentStuCount(val) {
      this.RFAbsentStuCount = val;
    },
    setRFLeaveStuCount(val) {
      this.RFLeaveStuCount = val;
    },
    setofflineclassname(val) {
      this.offlineclassname = val.label;
    }
  }
};
</script>
