            span {
              margin-right: 20px;
            }
          }
        }
        .scoreItem:last-child {
          margin-bottom: 0px;
        }
      }
    }

    .el-image {
      margin-right: 20px;
      border-radius: 3px;
    }
  }
}
.ed-wrap(@width) {
  width: @width;
  height: calc(100% - 22px);
  background: #fff;
  padding: 10px;
  .ed-head {
    margin-bottom: 15px;
    width: 100%;
    text-align: left;
    margin-top: 20px;
  }
  .ed-head:first-child {
    margin-top: 0px;
  }
}
</style>



